@media screen and (max-width: 991px) {
    //header
    .navbar {
        height: 4.375rem;
        padding: 0.9375rem 0;
        margin: 0;
        position: relative;
    }
    .navbar-brand {
        img {
            width: 80%;
        }
    }
    .navbar-collapse {
        position: fixed;
        top: 55px;
        left: 0px;
        display: flex;
        flex-direction: column;
        background-color: $burger-menu-background;
        margin: 15px 0 0 0;
        width: 100vw;
        box-shadow: 0 10vh 0 #282b38;
        transition: all 0.4s linear;
        z-index: 2;
        &::before {
            content: "";
            position: fixed;
            top: 70px;
            left: 0;
            width: 100%;
            height: 100%;
            backdrop-filter: blur(10px);
            z-index: -2;
        }
    }

    .navbar-nav {
        text-align: center;
        margin-top: 3.4375rem;
        padding: 0;
        height: calc(70vh - 55px);
    }
    .nav-item {
        margin: 0 0 48px 0;
        &:last-child {
            margin: 0 0 3rem 0;
        }
    }
    .nav-link--bs-white {
        font-size: 1.5rem;
        line-height: 222%;
        &:hover {
            &::after {
                content: "";
                position: absolute;
                display: none;
                width: 100%;
                height: 1px;
                border-radius: 1px;
                background-color: $after-background-color-lightgreen;
                left: 0;
                bottom: 0;
            }
        }
    }
    .nav-button {
        font-size: $global-font-size;
        padding: 1.4375rem 5.3125rem;
        margin-bottom: 3.75rem;
    }

    // first screen
    #first-screen {
        margin-bottom: 55px;

        .banner {
            &__container-fluid {
                padding: 0;
            }
            &__banner-item {
                display: flex;
            }
        }
        .banner-item {
            border-radius: 0;
            min-height: calc(100vh - 70px - 55px + 55px);
            padding: 90px 0;
            &::before {
                content: "";
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                background: linear-gradient(0deg, rgba(4, 5, 17, 0), rgba(4, 5, 17, 0.1));
            }
        }
        .banner-item {
            &__container {
                position: relative;
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;
                max-width: 1180px;
                height: 100%;
                margin: auto;
                margin-top: -10vh;
                padding: 0 20px;
                z-index: 1;
            }
            &__title {
                text-align: center;
                font-size: 2.25rem;
                max-width: 335px;
                margin-bottom: 10px;
            }
            &__subtitle {
                text-align: center;
                font-size: 1rem;
                margin-bottom: 18px;
                max-width: 335px;
            }
            &__button {
                margin: 0;
            }
        }
    }
    //about-company
    #about-company {
        margin-bottom: 2.125rem;
    }

    .about {
        &__header-company {
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
        }
        &__header-company-content {
            align-items: center;
            margin-bottom: 1.5625rem;
        }

        &__about-title {
            @include mobile-h2title(700, 1.5rem, 124.5%, uppercase);
            margin: 0 0 3.9375rem 0;
        }
        &__title {
            &::after {
                left: calc(50% - 2.6563rem);
            }
        }
        &__subtitle {
            text-align: justify;
            font-size: $global-font-size;
            line-height: 149%;
        }
    }

    //cooperation
    .cooperation {
        #cooperation {
            margin-bottom: 55px;
        }
        &__container-fluid {
            padding: 0;
        }
        &__bg {
            border-radius: 0px;
        }
        &__header {
            margin-bottom: 4.375rem;
        }
        &__header-title {
            @include mobile-h2title(700, 1.5rem, 124.5%, uppercase);
        }
        &__contnet-box {
            flex-direction: column-reverse;
            margin-bottom: 1.875rem;
        }
        &__info-list {
            margin-bottom: 1.8125rem;
        }
        &__info-item {
            font-size: 1rem;
        }
        &__image {
            margin-bottom: 1.875rem;
        }
        &__btn {
            padding: 1.4375rem 11.625rem 1.4375rem 2.0625rem;
            margin-top: 29px;
        }
    }
    .box-revers {
        flex-direction: column;
    }

    //footer
    .footer {
        &__inner {
            display: flex;
            justify-content: center;
        }
        &__contact {
            display: flex;
            flex-direction: column;
            align-items: center;
            text-align: center;
            margin: 0;
        }
        &__menu-block {
            display: none;
        }
    }
}
